.container {
  padding-left: 25px;
  padding-right: 25px;

  @media screen and (min-width: 320px) and (max-width: 767px) {
    width: 320px;
    margin-left: auto;
    margin-right: auto;
  }
  @media screen and (min-width: 768px) and (max-width: 1439px) {
    width: 768px;
    padding-left: 40px;
    padding-right: 40px;
    margin-left: auto;
    margin-right: auto;
  }
  @media screen and (min-width: 1440px) {
    width: 1440px;
    padding-left: 80px;
    padding-right: 80px;
    margin-left: auto;
    margin-right: auto;
  }
}

.wraper {
  background-size: 100%;
  background-image: url("../../image/bagrounImag320.svg");
  @media screen and (min-width: 768px) {
    background-image: url("../../image/backgroundimg768.svg");
  }
  @media screen and (min-width: 1440px) {
    background-image: url("../../image/backgroudimg1440.svg");
  }
}
